import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'

Vue.use(Vuex)

const state = {
  changeData: {
    menu: {
      errorCode: '全局错误码',
      apiList: '接口列表',
      model: '数据字典'
    },
    errorCode: {
      codeTip: '全局错误码',
      codeTitle: '错误码',
      codeExplain: '错误解释',
      success: '成功状态示例',
      fail: '失败状态示例',
      condition: '条件查询示例',
      key: '键',
      value: '值'
    },
    apis: {
      description: '简要描述',
      url: '请求URL',
      method: '请求方式',
      headerParams: 'header参数',
      params: 'params参数',
      queryParams: 'query参数',
      bodyParams: 'body参数',
      remark: '备注：更多返回错误代码请看首页的错误代码描述',
      name: '参数名',
      type: '类型',
      necessary: '必选',
      explain: '说明'
    },
    model: {
      field: '字段',
      type: '类型',
      description: '注释',
      remark: '备注：无'
    }
  }
}

export default new Vuex.Store({
  state,
  actions,
  mutations
})



