<template lang="html">
  <div class="page">
    <div class="page-item">
      <p class="title">
        {{changeData.errorCode.codeTip}}
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">{{changeData.errorCode.codeTitle}}</td>
          <td class="t-title">{{changeData.errorCode.codeExplain}}</td>
        </tr>
        <template v-for="(value,key,index) in project.ErrorCode">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        {{changeData.errorCode.success}}
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">{{changeData.errorCode.key}}</td>
          <td class="t-title">{{changeData.errorCode.value}}</td>
        </tr>
        <template v-for="(value,key,index) in project.apiSuccessTemplate">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        {{changeData.errorCode.fail}}
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">{{changeData.errorCode.key}}</td>
          <td class="t-title">{{changeData.errorCode.value}}</td>
        </tr>
        <template v-for="(value,key,index) in project.apiErrorTemplate">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        {{changeData.errorCode.condition}}
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">{{changeData.errorCode.key}}</td>
          <td class="t-title">{{changeData.errorCode.value}}</td>
        </tr>
        <template v-for="(value,key,index) in project.CommentQuery">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
  </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
  data() {
    return {
      project: {}
    }
  },
  computed: mapState(['changeData']),
  methods: {
    
  },
  async created() {
    let _this = this
    setTimeout(function(){
      _this.project = data.project
    },300)
  }
}
</script>
<style lang="scss" scoped>
  .page {
    margin-top: -24px;
    background-color: #f4f5f6;
    color: #020f18;
    padding-bottom: 50px;
    .page-item {
      width: calc(100% - 240px);
      background-color: #ffffff;
      margin: 60px 120px;
      padding-bottom: 40px;
      box-shadow: 0 0 18px rgba(0,62,103,0.10);
      p {
        width: calc(100% - 80px);
        margin: 20px 40px;
      }
      .title {
        height: 70px;
        line-height: 70px;
        font-size: 22px;
        color: #020f18;
        border-bottom: solid 1px #b9c4cf;
        font-family: "MicrosoftYaHei";
      }
      table {
        width: calc(100% - 80px);
        margin: 20px 40px;
        border:solid #E9EFF3; 
        border-width:1px 0px 0px 1px;
        td {
          width: 50%;
          border:solid #E9EFF3; 
          border-width:0px 1px 1px 0px;
          height: 35px;
          line-height: 35px;
          padding-left: 122px;
        }
        .t-title {
          background-color: #ecf3f8;
          color: #020f18;
          border-bottom: none;
        }
      }
    }
  }
</style>


