<template>
  <div class="top-bar">
    <el-switch
      :change="changeLanguage(value)"
      v-model=value
      on-text="中文"
      off-text="En"
      on-color="#13ce66"
      off-color="#ff4949">
    </el-switch>
  </div>
</template>
<script>
  import { mapActions } from 'vuex'
  export default {
    data() {
      return {
        value: true,
        chinese: {
          menu: {
            errorCode: '全局错误码',
            apiList: '接口列表',
            model: '数据字典'
          },
          errorCode: {
            codeTip: '全局错误码',
            codeTitle: '错误码',
            codeExplain: '错误解释',
            success: '成功状态示例',
            fail: '失败状态示例',
            condition: '条件查询示例',
            key: '键',
            value: '值'
          },
          apis: {
            description: '简要描述',
            url: '请求URL',
            method: '请求方式',
            headerParams: 'header参数',
            params: 'params参数',
            queryParams: 'query参数',
            bodyParams: 'body参数',
            remark: '备注：更多返回错误代码请看首页的错误代码描述',
            name: '参数名',
            type: '类型',
            necessary: '必选',
            explain: '说明'
          },
          model: {
            field: '字段',
            type: '类型',
            description: '注释',
            remark: '备注：无'
          }
        },
        english: {
          menu: {
            errorCode: 'ErrorCode',
            apiList: 'ApiList',
            model: 'Model'
          },
          errorCode: {
            codeTip: 'Error Code',
            codeTitle: 'code',
            codeExplain: 'description',
            success: 'Success Case',
            fail: 'Fail Case',
            condition: 'Condition Query Case',
            key: 'key',
            value: 'value'
          },
          apis: {
            description: 'Description',
            url: 'Request URL',
            method: 'Request Method',
            headerParams: 'HeaderParams',
            params: 'Params',
            queryParams: 'QueryParams',
            bodyParams: 'BodyParams',
            remark: 'Remark：For more error codes, see the error code description on the errorCode page',
            name: 'name',
            type: 'type',
            necessary: 'necessary',
            explain: 'explain'
          },
          model: {
            field: 'field',
            type: 'type',
            description: 'description',
            remark: 'Remark：none'
          }
        }
      }
    },
    methods: {
      ...mapActions([
  			'changeAction'
  		]),
      changeLanguage(value) {
        if (value) {
          this.changeAction(this.chinese)
        } else {
          this.changeAction(this.english)
        }
      }
    }
  }
</script>



